const ImageRound = (props) => {
    return (
        <div>
            <div className="image">
                <img className="image" src={props.src} alt={props.alt}/>
            </div>
            <style jsx>{`
                .image {
                    border-radius: 100%;
                    border: 1px solid;
                    height: 100px;
                    width: 100px;
                }
            `}</style>
        </div>
    )
}

export default ImageRound